.mobileNavBar * {
  box-sizing: border-box;
}

.mobileNavBar {
  display: inline-block;
  width: 100%;
  position: absolute;
  z-index: 100;
  /* border-bottom: 5px solid red; */
}

.mobileNavContent {
  display: block;
  height: 100%;
  /* height: fit-content; */
  overflow: hidden;
  width: 100%;
}

.mobileNavContent nav {
  /* border-bottom: 1px solid #ddd; */
  height: 46px;
  /* background: rgba(241, 241, 241, 0.488); */
  /* background: var(--background); */
  position: relative;
  border-bottom: 1px solid #ddd;
}
.mobileNavContent .nav-links {
  display: block;
  position: absolute;
  top: 46px;
  left: 0;
  width: 100%;
}
.mobileNavContent .nav-links::before {
  /* background: rgba(0, 0, 0, 0.5); */
  content: "";
  height: 100%;
  left: 0;
  top: 0;
  position: absolute;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  transition: all 0.3s;
}
.mobileNavContent .nav-links li {
  border-bottom: 1px solid #ddd;
  opacity: 0;
  visibility: hidden;
  -webkit-transform: translateY(-20px);
          transform: translateY(-20px);
  transition: all 0.3s;
}

.mobileNavContent .nav-links li span {
  /* background: #fff; */
  background: var(--background);

  color: #404040;
  display: block;
  font-size: 18px;
  font-weight: 700;
  padding: 12px 20px;
  text-align: left;
  width: 100%;
  transition: all 0.3s;
}

.mobileNavContent .nav-links li span:hover {
  color: lightgray;
}

#hamburgerCheckbox {
  position: absolute;
  opacity: 0;
  visibility: hidden;
}

#hamburgerCheckbox:checked + #labelhamburgerCheckbox {
  border: 4px solid silver;
  border-radius: 50%;
  height: 28px;
  top: 10px;
  left: 18px;
  width: 28px;
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
}

#hamburgerCheckbox:checked + #labelhamburgerCheckbox::before {
  background: silver;
  top: 8px;
  left: 4px;
  width: 12px;
}

#hamburgerCheckbox:checked + #labelhamburgerCheckbox::after {
  background: silver;
  opacity: 1;
  top: 8px;
  left: 4px;
  visibility: visible;
  width: 12px;
}

#hamburgerCheckbox:checked + #labelhamburgerCheckbox:hover {
  border-color: rgba(0, 0, 0, 0.5);
  /* border-color: red; */
}

#hamburgerCheckbox:checked + #labelhamburgerCheckbox:hover::before,
#hamburgerCheckbox:checked + #labelhamburgerCheckbox:hover::after {
  background: rgb(100, 100, 100);
}

#hamburgerCheckbox:checked ~ .nav-links::before {
  opacity: 1;
  visibility: visible;
}
#hamburgerCheckbox:checked ~ .nav-links li {
  opacity: 1;
  visibility: visible;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

#hamburgerCheckbox:checked ~ .nav-links li:nth-child(1) {
  transition-delay: 0s;
}
#hamburgerCheckbox:checked ~ .nav-links li:nth-child(2) {
  transition-delay: 0.1s;
}
#hamburgerCheckbox:checked ~ .nav-links li:nth-child(3) {
  transition-delay: 0.2s;
}
#hamburgerCheckbox:checked ~ .nav-links li:nth-child(4) {
  transition-delay: 0.3s;
}
#hamburgerCheckbox:checked ~ .nav-links li:nth-child(5) {
  transition-delay: 0.4s;
}

#labelhamburgerCheckbox {
  background: none transparent;
  border: 4px solid rgba(0, 0, 0, 0.5);
  border-left: 0 solid transparent;
  border-right: 0 solid transparent;
  cursor: pointer;
  display: block;
  height: 24px;
  position: absolute;
  top: 12px;
  left: 20px;
  width: 24px;
  transition: all 0.2s;
}
#labelhamburgerCheckbox::before {
  background: rgba(0, 0, 0, 0.5);
  content: "";
  height: 4px;
  left: 0;
  position: absolute;
  top: 6px;
  width: 24px;
  transition: all 0.2s;
}
#labelhamburgerCheckbox::after {
  background: lightgray;
  content: "";
  height: 4px;
  left: 0;
  position: absolute;
  top: 6px;
  opacity: 0;
  visibility: hidden;
  width: 100%;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  transition: all 0.2s;
}

